import axios from "axios";
import React, { useEffect, useState } from "react";
import { Button, ImageComp } from "../..";
import MyPlaylists from "../MyPlaylists/MyPlaylists";
import "./UserProfileModal.scss";

interface Props {
  selectedUser: string;
  onCloseClick?(...args: any): void;
}

const UserProfileModal = ({ selectedUser, onCloseClick }: Props) => {
  //   const [selectedUserState, setSelectedUserState] = useState(selectedUser);
  const [user, setUser] = useState<any>([]);
  const [playlist, setPlaylist] = useState<any>([]);
  const [playlistSong, setPlaylistSong] = useState<any>([]);

  const getUser = () => {
    const userId = selectedUser;
    axios
      .post(`${process.env.REACT_APP_BACKEND}/api/users/getSelectedUser`, {
        userId,
      })
      .then((res: any) => setUser(res.data.data));
  };

  useEffect(() => {
    getUser();
  }, []);
  return (
    <div className="user-profile-modal">
      <div className="user-profile-modal__container">
        <Button className="user-profile-modal__x" onClick={onCloseClick}>
          x
        </Button>
        <div className="user-profile-modal__user">
          <div>
            <ImageComp
              src={user.profileImage}
              className="user-profile-modal__image"
            />
          </div>
          <div className="user-profile-modal__details">
            <div>
              {user.firstName} {user.lastName}
            </div>
            <div>{user.username}</div>
          </div>
        </div>
        <MyPlaylists
          selectedUser={selectedUser}
          isXButton={false}
          likedSongs={false}
        />
      </div>
    </div>
  );
};

export default UserProfileModal;
