import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { Atag, Button, Contact } from "../..";
import "./Footer.scss";

const Footer = () => {
  const [email, setEmail] = useState(false);
  const [phone, setPhone] = useState(false);

  const handleCloseButton = () => {
    setEmail(false);
    setPhone(false);
  };

  return (
    <div>
      <div className="footer">
        <div className="footer__contact">
          <h2>Contact</h2>
          <div className="footer__contact-list">
            <NavLink to="/help" style={{ paddingBottom: "0.5rem" }}>
              Help
            </NavLink>
            <Button className="footer__button" onClick={() => setEmail(true)}>
              Email
            </Button>
            <Button className="footer__button" onClick={() => setPhone(true)}>
              Phone
            </Button>
          </div>
        </div>
        <div className="footer__review">
          <h2>Review</h2>
          <div className="footer__review-list">
            <NavLink to="/reviews" style={{ paddingBottom: "0.5rem" }}>
              Write a review
            </NavLink>
          </div>
        </div>
        <div className="footer__social">
          <h2>Socials</h2>
          <div className="footer__social-list">
            <Atag link={"facebookPage"} icon={"FacebookIcon"} />{" "}
            <Atag link={"instagramPage"} icon={"InstagramIcon"} />{" "}
            <Atag link={"twitterPage"} icon={"TwitterIcon"} />
          </div>
        </div>
      </div>
      {email && (
        <Contact
          onCloseClick={handleCloseButton}
          label="Email: "
          data="melissa.unal.tw@gmail.com"
        />
      )}
      {phone && (
        <Contact
          onCloseClick={handleCloseButton}
          label="Phone: "
          data="+40722000000"
        />
      )}
    </div>
  );
};

export default Footer;
