import React, { useContext, useEffect, useState } from "react";
// import "./SongList.scss";
import { Button, Song, AddToPlaylistModal } from "../../index";
import { FcLikePlaceholder, FcLike } from "react-icons/fc";
import { AudioContext } from "../../../context/AudioContext";
import { UserContext } from "../../../context/UserContext";
import { Pagination } from "@mui/material";
import axios from "axios";
import { makeStyles } from "@material-ui/core";
import { v4 as uuidv4 } from "uuid";
import * as constants from "../../../utils/constants/constants";
import { SearchResultContext } from "../../../context/SearchResultContext";

interface Props {
  playlistName: string;
  playlistId?: string;
  isXButton: boolean;
  selectedUser?: string;
}

const SongList = ({
  selectedUser,
  playlistName,
  playlistId,
  isXButton,
}: Props) => {
  const [currentPlaylist, setCurrentPlaylist] = useState<any>([]);
  const searchContextData = useContext(SearchResultContext);
  const userContextData = useContext(UserContext);
  const [page, setPage] = useState(1);
  // const ITEMS_PER_PAGE = 5;

  const getAllPlaylists = async () => {
    const userId = selectedUser;
    await axios
      .post(`${process.env.REACT_APP_BACKEND}/api/audio/allPlaylists`, {
        userId,
      })
      .then((res: any) => {
        setCurrentPlaylist(res.data.data);
      })
      .catch((err) => console.log(err));
  };

  const getLikedAudio = () => {
    const userId = selectedUser;
    axios
      .post(`${process.env.REACT_APP_BACKEND}/api/like/userLike`, { userId })
      .then((res: any) => {
        setCurrentPlaylist(res.data.data);
      })
      .catch((err) => console.log(err));
  };

  const getPlaylistSong = async () => {
    const userId = selectedUser;
    await axios
      .post(
        `${process.env.REACT_APP_BACKEND}/api/playlistSong/allPlaylistSong`,
        {
          userId,
          playlistId,
        }
      )
      .then((res: any) => {
        setCurrentPlaylist(res.data.data);
      })
      .catch((err) => console.log(err));
  };

  const getSearched = () => {
    setCurrentPlaylist(searchContextData.searchData);
  };

  useEffect(() => {
    if (playlistId) getPlaylistSong();
    else {
      if (playlistName === "All songs") {
        getAllPlaylists();
      }
      if (playlistName === "Liked songs") {
        getLikedAudio();
      }
      if (playlistName === "Search") {
        getSearched();
      }
    }
  }, [playlistId, playlistName]);

  const useStyles = makeStyles((theme) => ({
    root: {
      "& .MuiPaginationItem-root": {
        color: "#ffff",
      },
      "& .css-19micn4-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected": {
        backgroundColor: "#ef5c19",
      },
      "& .css-19micn4-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected:hover":
        {
          backgroundColor: "#ef5c19",
        },
      "& .css-1kz0k3h.Mui-selected": {
        backgroundColor: "#ef5c19",
      },
      "& .css-1kz0k3h.Mui-selected:hover": {
        backgroundColor: "#ef5c19",
      },
    },
  }));

  const classes = useStyles();

  const handleDeleteFromPlaylist = async (songId: string) => {
    const userId = selectedUser;
    await axios
      .delete(
        `${process.env.REACT_APP_BACKEND}/api/playlistSong/deletePlaylistSong`,
        {
          data: {
            songId,
            userId,
            playlistId,
          },
        }
      )
      .then(() => getPlaylistSong());
  };

  const handleChangePage = (
    event: React.ChangeEvent<unknown>,
    value: number
  ) => {
    setPage(value);
  };

  const getNumberOfPages = (numberOfItems: number, itemsPerPage: number) => {
    if (itemsPerPage === numberOfItems) {
      return 1;
    } else {
      return Math.trunc(numberOfItems / itemsPerPage) + 1;
    }
  };

  let lastPage = currentPlaylist.length / constants.ITEMS_PER_PAGE;

  if (lastPage % 1 !== 0) {
    lastPage = Math.trunc(lastPage) + 1;
  }

  const getPageItems = (pageNumber: number, numberOfPages: number) => {
    let arr = [];

    if (numberOfPages === 1) {
      return currentPlaylist;
    } else {
      // const startIndex = ITEMS_PER_PAGE * (pageNumber - 1);
      // const endIndex = currentPlaylist.length - 1;
      // if (startIndex === endIndex) {
      //   arr = currentPlaylist.slice(-1);
      // }

      if (pageNumber === lastPage) {
        arr = currentPlaylist.slice(
          constants.ITEMS_PER_PAGE * (pageNumber - 1),
          currentPlaylist.length
        );
      } else {
        arr = currentPlaylist.slice(
          constants.ITEMS_PER_PAGE * (pageNumber - 1),
          constants.ITEMS_PER_PAGE * pageNumber
        );
      }
    }

    // if (arr.length > ITEMS_PER_PAGE) {
    //   arr = arr.slice(ITEMS_PER_PAGE, arr.length - 1);
    // }
    return arr;
  };

  const numberOfPages = getNumberOfPages(
    currentPlaylist.length,
    constants.ITEMS_PER_PAGE
  );
  const pageItems = getPageItems(page, numberOfPages);
  console.log(selectedUser);
  const getSongsListHTML = (songs: any) => {
    if (!songs || songs.length === 0) {
      return [];
    }
    return songs.map((value: any) => {
      return (
        <Song
          key={
            "song_id_" +
            value._id +
            "_playlist_id_" +
            playlistId +
            `${uuidv4()}`
          }
          isXButton={
            playlistId && selectedUser === userContextData.userId ? true : false
          }
          currentPlaylist={currentPlaylist}
          song={value}
          onClickDeleteFromPlaylist={() => handleDeleteFromPlaylist(value._id)}
          getLikedAudio={getLikedAudio}
        />
      );
    });
  };

  return (
    <div>
      <div style={{ height: "31rem" }}>{getSongsListHTML(pageItems)}</div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          paddingTop: "2rem",
          bottom: "15%",
          left: "0%",
          right: "0%",
        }}
      >
        <Pagination
          count={numberOfPages}
          className={classes.root}
          color="secondary"
          page={page}
          defaultPage={1}
          onChange={handleChangePage}
        />
      </div>
    </div>
  );
};

export default SongList;
