import React from "react";
import { NavLink } from "react-router-dom";
import { Button, SearchBar } from "../..";
import "./Header.scss";

const Header = () => {
  return (
    <header>
      <div className="header__background-image">
        <div className="header__buttons-flex">
          <NavLink to="/" className="header__link">
            <Button>Home</Button>
          </NavLink>

          <NavLink to="/trending" className="header__link">
            <Button>Trending</Button>
          </NavLink>

          <NavLink to="/map" className="header__link">
            <Button>Map</Button>
          </NavLink>
        </div>
        <div style={{ paddingTop: "1rem" }}>
          <SearchBar />
        </div>
      </div>
    </header>
  );
};

export default Header;
