import axios from "axios";
import React, { useEffect, useState } from "react";
import { FcMusic } from "react-icons/fc";
import { Song } from "../..";
import "./Trending.scss";

const Trending = () => {
  const [trending, setTrending] = useState<any>([]);
  const getTrending = () => {
    axios
      .get(`${process.env.REACT_APP_BACKEND}/api/audio/trending`)
      .then((res: any) => setTrending(res.data.data));
  };
  useEffect(() => {
    getTrending();
  }, []);
  return (
    <div className="trending">
      <div className="trending__container">
        <h1 className="trending__title">Our current top 5...</h1>
        {trending.map((value: any) => {
          return (
            <div className="trending__list">
              <span className="trending__count">
                <FcMusic /> {value.timesPlayed} times
              </span>
              <Song isXButton={false} song={value} currentPlaylist={trending} />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Trending;
